import React, { useContext, useEffect, useState } from 'react'
import useApi from '../../Hooks/useApi'
import { useNavigate, useParams } from 'react-router-dom'
import Slider from 'react-slick'
import { v4 as uuidv4 } from 'uuid';
import { CartContext } from '../../Context/CartContext';
import { toast } from 'react-hot-toast';
export default function ProductsDetails() {
  let navigate = useNavigate()
  let settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  const { id } = useParams()
  const { products } = useApi(`https://ecommerce.routemisr.com/api/v1/products/${id}`)
  
 const {addToCart} = useContext(CartContext)

  async function addToCartFun(id)
  {
    if(localStorage.getItem('token'))
    {
      let data =  await addToCart(id)
      if(data.status == 'success')
       {
         toast.success('your product added successful')
       }
       else
       {
         toast.error(data.message)
       }
    }
    else
    navigate('/login')
  }

  return (

    <div className="row my-5 justify-content-between">
      <div className="col-md-3">
        {products.images ? <Slider {...settings} >
          {products.images.map((ele) =>
          <div className='position-relative bg-info d-block' key={ele}>
              < img key={uuidv4()} src={ele} alt={ele.name} className='w-100'/>
          
          </div>
          )}
        </Slider> : 'loading'}
      </div>
      
      <div className="col-md-8 d-flex flex-column justify-content-center">
        <p>{products.description}</p>
        <p>{products.title}</p>
        <div className="box d-flex justify-content-between align-items-center my-4">
          {products.subcategory?<p className='fw-bold'>{products.subcategory[0].name}</p>:''}
          <p>{products.ratingsAverage} <i className='fa-solid fa-star text-warning'></i></p>
        </div>
        <p>{products.price}$</p>
      
        <button className='btn btn-outline-success form-control my-3' onClick={()=>{addToCartFun(products._id)}}>Add to cart</button>
      </div>
    </div>
  )
}
